"use client";
import React, { useEffect, useRef, useState } from "react";
import { Room } from "livekit-client";
import Draggable from "react-draggable";
// Change this line:
import "./VideoCall.css"; // to:
// Next.js uses CSS modules by default, so let's import it properly:
import styles from "./VideoCall.module.css"; // You'll need to rename your CSS file

const VideoCall = ({ roomUrl, onClose }) => {
  const videoContainer = useRef(null);
  const [room, setRoom] = useState(null);
  
  useEffect(() => {
    const joinRoom = async () => {
      const newRoom = new Room();
      await newRoom.connect(roomUrl, {
        video: true,
        audio: true,
      });
      
      setRoom(newRoom);
      
      newRoom.localParticipant.videoTracks.forEach((trackPublication) => {
        if (trackPublication.track) {
          trackPublication.track.attach(videoContainer.current);
        }
      });
    };
    
    joinRoom();
    
    return () => {
      if (room) {
        room.disconnect();
      }
    };
  }, [roomUrl, room]);
  
  return (
    // Use the imported styles
    <div className={styles["video-call-wrapper"]}>
      <Draggable bounds="parent">
        <div className={styles["video-popup"]}>
          <div ref={videoContainer} className={styles["video-container"]}></div>
          <button className={styles["close-btn"]} onClick={onClose}>
            Close
          </button>
        </div>
      </Draggable>
    </div>
  );
};

export default VideoCall;